import React from "react";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

const ProjectsPageTile = (props) => {
    
    const image = getImage(props.backgroundImage);

    return (
        <a href={props.url}>
            <section className={"projectsPageTile " + props.name + (props.mobile ? " projectsPageTileMobile" : "")}>
                <p className={"projectsPageTileText " + props.name + "Text"}>{props.title}</p>
                <div className={"projectsPageTileOverlay " + props.name + "Overlay"}></div>
                <GatsbyImage image={image} alt={props.title} className={"projectsPageTileImage " + props.name + "Image"} />
            </section>
        </a>
    )
}

export default ProjectsPageTile;