import React, { useEffect, useState } from "react";
import { graphql } from "gatsby";

import NavBar from "../components/navbar";
import ProjectsPageTile from "../components/projectsComponents/projectsPageTile";
import ProjectsPageFiller from "../components/projectsComponents/projectsPageFiller";
import MobileNav from "../mobileComponents/mobileNav";
import MobileFooter from "../mobileComponents/mobileFooter";
import "../components/projectsComponents/projectsPage.css";
import "../components/projectsComponents/projectsPageTile.css";
import "../components/projectsComponents/projectsPageFiller.css";
import "../components/projectsComponents/arrows.css";
import "../mobileComponents/mobileProjects/projectsMobile.css";

const ProjectsIndexPage = ({ data }) => {

    const [pressedKeys, setPressedKeys] = useState([]);

    useEffect(() => {
        const ALLOWED_KEYS = ['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'];
        const onKeyDown = ({key}) => {
            carouselPicker(key);
            if (ALLOWED_KEYS.includes(key) && !pressedKeys.includes(key)) {
                setPressedKeys(previousPressedKeys => [...previousPressedKeys, key]);
            }
        }

        const onKeyUp = ({key}) => {
            if (ALLOWED_KEYS.includes(key)) {
                setPressedKeys(previousPressedKeys => previousPressedKeys.filter(k => k !== key));
            }
        }

        document.addEventListener('keydown', onKeyDown);
        document.addEventListener('keyup', onKeyUp);

        return () => {
            document.removeEventListener('keydown', onKeyDown);
            document.removeEventListener('keyup', onKeyUp);
        }
    }, []);

    const [projects, setProjects] = useState(
        [
            <ProjectsPageTile 
                url="https://mkepressurewashing.com/"
                name="mkeProjectTile"
                title="MKE Pressure Washing"
                backgroundImage={data.matthewSite}
                mobile={false}
            />,
            <ProjectsPageTile
                url="https://github.com/JackLudwikoski/AnalogClock"
                name="analogClockProjectTile"
                title="Analog Clock"
                backgroundImage={data.analogClock}
                mobile={false}
             />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />,
            <ProjectsPageFiller mobile={false} />
        ]
    )

    const [mobileProjects, setMobileProjects] = useState(
        [
            <ProjectsPageTile 
                url="https://mkepressurewashing.com/"
                name="mkeProjectTile"
                title="MKE Pressure Washing"
                backgroundImage={data.matthewSite}
                mobile={true}
            />,
            <ProjectsPageTile
                url="https://github.com/JackLudwikoski/AnalogClock"
                name="analogClockProjectTile"
                title="Analog Clock"
                backgroundImage={data.analogClock}
                mobile={true}
             />,
            <ProjectsPageFiller mobile={true} />
        ]
    );

    useEffect(() => {
        const element = document.getElementById("projectsBullet");
        element.classList.add("filled");
    })

    const carouselLeft = () => {
        const array = projects;
        let temp1 = array[0];
        let temp2;
        array[0] = array[array.length - 1];
        for (let i = 1; i < array.length; i++) {
            temp2 = array[i];
            array[i] = temp1;
            temp1 = temp2;
        }
        setProjects([...array]);

        const array2 = mobileProjects;
        temp1 = array2[0];
        array2[0] = array2[array2.length - 1];
        for (let i = 1; i < array2.length; i++) {
            temp2 = array2[i];
            array2[i] = temp1;
            temp1 = temp2;
        }
        setMobileProjects([...array2]);
    }

    const carouselRight = () => {
        const array = projects;
        let temp1 = array[array.length - 1];
        let temp2;
        array[array.length - 1] = array[0];
        for (let i = array.length - 2; i >= 0; i--) {
            temp2 = array[i];
            array[i] = temp1;
            temp1 = temp2;
        }
        setProjects([...array]);

        const array2 = mobileProjects;
        temp1 = array2[array2.length - 1];
        array2[array2.length - 1] = array2[0];
        for (let i = array2.length - 2; i >= 0; i--) {
            temp2 = array2[i];
            array2[i] = temp1;
            temp1 = temp2;
        }
        setMobileProjects([...array2]);
    }

    const carouselPicker = (ev) => {
        if (ev === "ArrowRight") {
            //left
            carouselLeft();
        }
        else if(ev === "ArrowLeft") {
            //right
            carouselRight();
        }
    }
    
    return (
        <>
            <div className="projectsUpperHalf">
                <section className="topProjectCont">
                    {projects[0]}
                    {projects[1]}
                    {projects[2]}
                    {projects[3]}
                    {projects[4]}
                </section>
                <section className="leftProjectCont">
                    {projects[9]}
                    {projects[8]}
                </section>
                <section className="mainTextCont">
                    <h1 className="projectsPageText">Projects</h1>
                </section>
                <section className="rightProjectCont">
                    {projects[5]}
                </section>
            </div>
            <div className="projectsLowerHalf">
                <div className="projectsLowerHalfLeft"></div>
                <div className="projectsLowerHalfRight">
                    <section className="bottomProjectsRow">
                        <section className="projectsButtonsCont">
                            <div className="leftArrow" onClick={() => carouselRight()}/>
                            <div className="rightArrow" onClick={() => carouselLeft()}/>
                        </section>
                        <section className="bottomProjectCont">
                            {projects[7]}
                            {projects[6]}
                        </section>
                    </section>
                    <section className="projectsContactCont">
                        <p className="projectsContactInfoPhone">Phone Number: <a href="tel:4147318874"><em>414-731-8874</em></a></p>
                        <p className="projectsContactInfoEmail">Email: <a href="mailto:jdludwikoski@gmail.com"><em>jdludwikoski@gmail.com</em></a></p>
                    </section>
                </div>
            </div>
            <NavBar />
            <MobileNav backgroundImage={data.siteLogoDark} currentPage="Projects" />
            <div className="mobileProjectsPageCont">
                <section className="mobileProjectsCont">
                    {mobileProjects[0]}
                    {mobileProjects[1]}
                    {mobileProjects[2]}
                </section>
                <section className="mobileProjectsButtons">
                    <div className="mobileLeftArrow" onClick={() => carouselLeft()} />
                    <div className="mobileRightArrow" onClick={() => carouselRight()} />
                </section>
            </div>
            <MobileFooter currentPage="Projects" backToTop={false} />

        </>
            
    );
}

export const query = graphql`
    query MyProjectsQuery {
        matthewSite: file(relativeDirectory: {eq: "projectsImages"} name: {eq: "Matthew Site Home Page NEWEST"})
            {
                childImageSharp {
                    gatsbyImageData(formats: [AUTO, WEBP, AVIF] placeholder: BLURRED width: 300)
                }
            }
        analogClock: file(relativeDirectory: {eq: "projectsImages"} name: {eq: "AnalogClockProjectCroppedRect"})
            {
                childImageSharp {
                    gatsbyImageData(formats: [AUTO, WEBP, AVIF] placeholder: BLURRED width: 300)
                }
            }
        siteLogoDark: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "SitesByJackLogo Dark No Background Smaller"})
            {
              childImageSharp {
                gatsbyImageData(width: 500 placeholder: BLURRED formats: [AUTO, WEBP, AVIF])
              }
            }
    }
`

export default ProjectsIndexPage